@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.header{
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin: 1rem 0;
}

.header >  ul > li {
    display: inline;
}


.header >  ul{
    width: 35%;
    /* background-color: gray; */
    display: flex;
    justify-content: space-between;
}

.mainTitle{
    text-decoration: none;
    color: black;
    font-size: 2em;
    font-weight: bold;
}

.firstLetter{
    font-size: 2.8rem;
    border-top: thick solid black;
    border-bottom: thick solid black;
    transition: transform 2s;
    padding: 0;
}


.mainTitle:hover{
    color: #666;
    border-color: #666;
}

.nav-link{
    text-decoration: none;
    font-size: 1.3em;
    font-weight: bold;
    color: black;
}

.nav-link:hover{
    color: #555;
    border-bottom: medium solid #555;
}

.active{
    color: #777;
}

@media (max-width: 1024px) {
    /* .header{
        width: 90%;
    } */
    
}

@media (max-width: 800px){
    .header > ul{
        width: 50%;
    }
}

@media (max-width: 420px) {
    .header{
        flex-direction: column;
    }

    .mainTitle{
        font-size: 1.3em;
        align-self: center;
    }
    .firstLetter{
        font-size: 1.5em;
    }
    .header > ul {
        width: 100%;
        margin-top: 1.5rem;
    }
    .nav-link{
        font-size: 1em;
    }

}

.home-container{
    margin-top: 3rem;
    display: flex;
    /* background-color: gray; */
}

.home-text{
    width: 50%;
    line-height: 1.3;
    margin-top: 3rem;
}

.home-text > p{
    font-size: 2.5em;
    font-weight: bold;
}

.home-text > span{
    font-size: 1.8em;
    font-weight: bold;
}

.home-to-work{
    position: relative;
    top: 3.5rem;
    text-decoration: none;
    color: white;
    background-color: #1167b1;
    font-size: 1.5em;
    font-weight: bold;
    border: thick solid #1167b1;
    padding: 0.7rem;
    margin-bottom: 1rem;
    border-radius: 35px 10px 35px 10px;
}

.home-to-work:hover{
    background-color: white;
    color: #1167b1;
}

.home-image{
    width: 50%;
    height: auto;
}

@-webkit-keyframes upRightDown{
    0%{
        bottom: 0px;
        left: 0px;
    }
    50%{
        bottom: 20px;
        left:20px;
    }
    100%{
        bottom: 0px;
        left: 0px;
    }
}

@keyframes upRightDown{
    0%{
        bottom: 0px;
        left: 0px;
    }
    50%{
        bottom: 20px;
        left:20px;
    }
    100%{
        bottom: 0px;
        left: 0px;
    }
}

.home-image > img {
    max-width: 100%;
    position: relative;
    -webkit-animation: upRightDown 4s;
            animation: upRightDown 4s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@media (max-width: 1024px) {
    .home-text > p{
        font-size: 2em;
        font-weight: bold;
    }   
    .home-text > span{
        font-size: 1.5em;
    }
    .home-to-work{
        font-size: 1.5em;
        top: 2rem;
    }
}

@media (max-width: 800px) {
    .home-text > p{
        font-size: 1.5em;
        font-weight: bold;
    }   
    .home-text > span{
        font-size: 1em;
    }
    .home-to-work{
        font-size: 1em;
        top: 2rem;
    }
}

@media (max-width: 420px) {
    .home-container{
        flex-direction: column-reverse;
        margin-top: 1rem;
    }   
    .home-text{
        width: 100%;
        align-self: center;
        margin-top: 0;
        text-align: center;
        line-height: 1.5;
    }
   
    .home-image{
        width: 100%;
        margin-top:2rem;
    }

    @-webkit-keyframes upRightDown{
        0%{
            bottom: 0px;
        }
        50%{
            bottom: 20px;
        }
        100%{
            bottom: 0px;
        }
    }

    @keyframes upRightDown{
        0%{
            bottom: 0px;
        }
        50%{
            bottom: 20px;
        }
        100%{
            bottom: 0px;
        }
    }
}
.repo-link {
    text-decoration: none;

}

.repo-link-container{
    /* background-color: black; */
    display: inline-block;
    width: 200px;
    text-align: center;
}
.repo-link-container > img{
    display: block;
    margin: 0 auto;
    width: 120px;
    height: 90px;
}

@media (max-width: 420px) {
    .repo-link-container{
        width: 100px;
        margin: 1rem;
    }
    .repo-link-container > img {
        width: 80px;
        height: 60px;
    }
    .repo-link-container > h3 {
        font-size: 1em;
    }
}
.project-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
}

.repo-list-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-items: self-start;
}

.error {
    width: 200px;
    border: solid red;
    border-radius: 10px;
    text-align: center;
    margin: 0 auto;
    padding: 0.3rem;
    background-color: lightcoral;
}

.work-to-about {
    text-decoration: none;
    color: white;
    font-weight: bold;
    width: 100px;
    border: solid lightseagreen;
    display: block;
    margin: 4rem auto;
    text-align: center;
    padding: 0.5rem;
    font-size: 1em;
    border-radius: 10px;
    outline: none;
    background-color: lightseagreen;
}

@media (max-width: 800px) {
    .repo-list-container {
        grid-template-columns: 1fr 1fr 1fr;
    }
    .project-list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
}

@media (max-width: 420px) {
    .repo-list-container{
        grid-template-columns: 1fr 1fr;
        justify-items: center;
    }
    .project-list{
        justify-content: center;
    }
}
.about-container{
    display: flex;
    justify-content: space-between;
    margin-top: 3rem;
}

.about-image{
    width: 50%;
    height: auto;
}

.about-content{
    width: 50%;
    padding: 2rem;
}

@-webkit-keyframes upDown{
    0%{
        bottom: 0px;
    }
    50%{
        bottom: 20px;
    }
    100%{
        bottom: 0px;
    }
}

@keyframes upDown{
    0%{
        bottom: 0px;
    }
    50%{
        bottom: 20px;
    }
    100%{
        bottom: 0px;
    }
}

.about-img {
    transform: scaleX(-1);
    max-width: 100%;
    position: relative;
    -webkit-animation: upDown 4s;
            animation: upDown 4s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

.about-mainText > h1{
    margin: 2rem 0 1.5rem 0;
    width: 145px;
    border-bottom: thick solid black;
}

.about-mainText > p{
    text-indent: 5%;
    font-size: 1.6em;
    line-height: 1.3;
    text-align: justify;
}

.about-skills > h1{
    margin: 2rem 0 1.5rem 0;
    width: 80px;
    border-bottom: thick solid black;
}

.logo-img{
    width: 70px;
    height: 70px;
    margin: 1rem;
}

.mongodb{
    width: 120px !important;
}

.about-to-contact{
    /* position: relative;
    top: 1.5rem;
    left: 1rem;
    bottom: 50px; */
    display: inline-block;
    margin: 1.5rem 1rem;
    text-decoration: none;
    color: white;
    background-color: crimson;
    font-size: 1em;
    font-weight: bold;
    border: thick solid crimson;
    padding: 0.5rem;
    /* border-radius: 35px 10px 35px 10px; */
    border-radius: 10px;
}

@media (max-width: 800px) {
    .about-container{
        flex-direction: column;
    }
    .about-image{
        width: 100%;
    }
    .about-content{
        width: 100%;
        padding: 0;
    }
    .about-mainText > h1{
        font-size: 1.2em;
        width: 86px;
        margin-left: 0;
    }
    .about-mainText > p{
        font-size: 1.2em;

    }
    .about-skills > h1{
        font-size: 1.2em;
        margin-left: 0;
        width: 45px;
    }
    .logo-img{
        margin: 0.7rem;
    }
    .about-to-contact{
        /* margin: 0 auto; */
        display: block;
        width: 100px;
        margin: 2rem auto;
        font-size: 1em;
        padding: 0.4rem;
        text-align: center;
    }
}
.contact-container{
    padding-top: 2rem;
}

.error{
    width: 200px;
    border: solid red;
    border-radius: 10px;
    text-align: center;
    margin: 0 auto;
    padding: 0.3rem;
    background-color: lightcoral;
}

.success{
    width: 200px;
    border: solid green;
    border-radius: 10px;
    text-align: center;
    margin: 0 auto;
    padding: 0.3rem;
    background-color: lightgreen;
}

.contact-container > h2{
    text-align: center;
    margin: 1rem;
}

.contact-form-container{
    width: 40%;
    margin: 0 auto;
}

.contact-form-container > input {
    width: 95%;
    margin: 0.1rem auto;
    padding: 0.5rem;
    font-size: 1em;
    box-sizing: border-box;
    border-radius: 10px;
    border-color: black;
    outline-style: none;
    display: block;
}

.contact-form-container > input:focus {
    outline: none;
}

.contact-form-container > textarea {
    width: 95%;
    resize: none;
    display: block;
    margin: 0.1rem auto;
    padding: 0.5rem;
    font-size: 1em;
    box-sizing: border-box;
    height: 150px;
    border-radius: 10px;
    border-color: black;
    outline-style: none;
    border-width: 2px;
}

.contact-form-container > button {
    display: block;
    width: 100px;
    margin: 0 auto;
    padding: 0.3rem;
    font-size: 1em;
    font-weight: bold;
    border-radius: 10px;
    border-color: black;
    color: white;
    background-color: black;
    cursor: pointer;
}

.contact-container > h1{
    text-align: center;
    margin: 1rem;
}

.contact-container > h3{
    text-align: center;
    margin: 1rem;
}

.contact-text {
    margin-bottom: 2rem;
}

.emailAdd{
    color: gray;
    font-size: 1.2em;
    margin-bottom: 2rem;
}

@media (max-width: 1024px) {
    .contact-form-container {
        width: 60%;
    }
}

@media (max-width: 420px) {
    .contact-container{
        padding-top: 0.5rem;
    }
    .contact-container > h3 {
        margin: 0.5rem;

    }
    .contact-form-container{
        width: 100%;
    }
}
.main-container{
    width: 80%;
    margin: 0 auto;
}

*{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif, serif; 
}

